.App {
  max-width: 860px;
  margin: 40px auto;
}
button {
  background: none;
  border: 2px solid #fff;
  padding: 6px 12px;
  border-radius: 4px;
  color: #006666;
  font-weight: bold;
  cursor: pointer;
  font-size: 1em;
}
button:hover {
  background: #476b6b;
  color: #fff;
}
.card-grid{
  margin-top: 40px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap:20px
}

